import http from "../http-common";

class CustomerDataService {
    // Î™®Îì† Í≥†Í∞ùÏ†ïÎ≥¥ Ï°∞Ìöå ÏöîÏ≤≠ Ìï®Ïàò + Ïù¥Î©îÏùº Í≤ÄÏÉâ ÏöîÏ≤≠ Ìï®Ïàò 
    // email : Ïù¥Î©îÏùº
    // page : ÌòÑÏû¨ ÌéòÏù¥ÏßÄ Î≤àÌò∏ 
    // size : Ìïú ÌéòÏù¥ÏßÄÎãπ ÌôîÎ©¥Ïóê ÌëúÏãúÌï† Îç∞Ïù¥ÌÑ∞ Í∞ØÏàò
    getAll(email, page, size) {
    //   alert("ÌôïÏù∏Ïö©2")

        // get Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @GetMapping("/api/customer")
        // 1) Ï†ÑÏ≤¥ Ï°∞Ìöå ÏôÄ
        // 2) Ïù¥Î©îÏùº Ï°∞ÌöåÎ•º Í∞ôÏù¥ ÌïòÎäî Ìï®Ïàò getAll
        return http.get(`/customer?email=${email}&page=${page}&size=${size}`); 
    }

    // CIDÎ°ú Ï°∞Ìöå ÏöîÏ≤≠ Ìï®Ïàò
    // get Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @GetMapping("/api/customer/{cid}"), @PathVariable
    get(cid) {
        return http.get(`/customer/${cid}`)
    }

    // Í≥†Í∞ùÏ†ïÎ≥¥ ÏÉùÏÑ±(insert) ÏöîÏ≤≠ Ìï®Ïàò
    // post Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @PostMapping("/api/customer"), @RequestBody
    create(data) {
        console.log(data);
        return http.post("/customer", data);
    }

    // Í≥†Í∞ùÏ†ïÎ≥¥ ÏàòÏ†ï(update) ÏöîÏ≤≠ Ìï®Ïàò
    // put Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @PutMapping("/api/customer/{cid}"), @RequestBody
    update(cid, data) {
        return http.put(`/customer/${cid}`, data);
    }

    // Í≥†Í∞ùÏ†ïÎ≥¥ ÏÇ≠Ï†ú(delete) ÏöîÏ≤≠ Ìï®Ïàò
    // delete Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @DeleteMapping("/api/customer/deletion/{cid}")
    //                        , @PathVariable  
    delete(cid) {
        return http.delete(`/customer/deletion/${cid}`);
    }

    // // Í≥†Í∞ùÏ†ïÎ≥¥ Ï†ÑÏ≤¥ ÏÇ≠Ï†ú ÏöîÏ≤≠ Ìï®Ïàò
    // // delete Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @DeleteMapping("/api/customer/all")
    // deleteAll() {
    //     return http.delete("/customer/all")
    // }

}

export default new CustomerDataService;