<template>
  <!-- AddCustomer.vue -->
  <div class="submit-form">
    <!-- ÏÉà ÏñëÏãù Ìèº ÏãúÏûë -->
    <div v-if="!submitted">
      <div class="margin">
      <!-- ÏÑ± -->
      <div class="form-group">
        <label for="lastName">Last Name</label>
        <input
          type="text"
          class="form-control"
          id="email"
          required
          v-model="customer.lastName"
          name="lastName"
        />
      </div>
      
      <!-- Ïù¥Î¶Ñ -->
      <div class="form-group">
        <label for="firstName">First Name</label>
        <input
          type="text"
          class="form-control"
          id="firstName"
          required
          v-model="customer.firstName"
          name="firstName"
        />
      </div>
      
      <!-- Ïù¥Î©îÏùº -->
      <div class="form-group">
        <label for="email">Email</label>
        <input
          type="text"
          class="form-control"
          id="email"
          required
          v-model="customer.email"
          name="email"
        />
      </div>

      <!-- Ìú¥ÎåÄÏ†ÑÌôî -->
      <div class="form-group">
        <label for="phone">Phone</label>
        <input
          class="form-control"
          id="phone"
          required
          v-model="customer.phone"
          name="phone"
        />
      </div>
    </div>
      <!-- ÏÉàÎ°úÏö¥ Í≥†Í∞ùÏ†ïÎ≥¥ ÏûëÏÑ± ÌõÑ Ï†úÏ∂úÎ≤ÑÌäº -->
      <button @click="saveCustomer" class="btn btn-success">Submit</button>
    </div>
    <!-- ÏÉà ÏñëÏãù Ìèº ÎÅù -->
    
    <!-- ÏïΩÍ∞Ñ ÎîîÏûêÏù∏ Í∞úÏÑ†Ìï® -->
    <div v-else>
      <div class="alert alert-success" role="alert">
        Save customer successfully!
      </div>
      <!-- Ï∂îÍ∞Ä Îì±Î°ù Î≤ÑÌäº -->
      <button @click="newCustomer" class="btn btn-primary">
        Add New Customer
      </button>
    </div>
  </div>

</template>

<script>
// üëÄaxios Í≥µÌÜµÌï®Ïàò(springboot Ïó∞ÎèôÏùÑ ÏúÑÌïú Ìï®ÏàòÎì§Ïùò Î™®ÏûÑ) import ÌïÑÏàò!!
import CustomerDataService from "../../services/CustomerDataService";

export default {
  data() {
    return {
      customer: {
        lastName: "",     //string ÏùÄ Ï¥àÍ∏∞Í∞íÏÑ∏ÌåÖ "" (int Îäî null)
        firstName: "",
        email: "",
        phone: "",
        cid: null
      },
      // submit Î≤ÑÌäºÏùÑ ÌÅ¥Î¶≠ÌïòÎ©¥ true(Î∞±Ïóî, insert)Í∞Ä ÎêòÍ≥†, You submitted successfully! ÌôîÎ©¥Ïóê Ï∂úÎ†•Îê®
      submitted: false
    };
  },
  methods: {
    saveCustomer() {
      // ÏûÑÏãú Í∞ùÏ≤¥ Î≥ÄÏàò -> springboot Ï†ÑÏÜ°
      // Î≤àÌò∏Îäî(cid) ÏûêÎèôÏÉùÏÑ±ÎêòÎØÄÎ°ú ÎπºÍ≥† Ï†ÑÏÜ°Ìï®
      let data = {
        lastName: this.customer.lastName,
        firstName: this.customer.firstName,
        email: this.customer.email,
        phone: this.customer.phone,
      };

      // insert ÏöîÏ≤≠ Ìï®Ïàò Ìò∏Ï∂ú(axios Í≥µÌÜµÌï®Ïàò Ìò∏Ï∂ú)
      CustomerDataService.create(data)
      // ÏÑ±Í≥µÌïòÎ©¥ then() Í≤∞Í≥ºÍ∞Ä Ï†ÑÏÜ°Îê®
      .then(response => {
        this.customer.cid = response.data.cid;
        // ÏΩòÏÜî Î°úÍ∑∏ Ï∂úÎ†•(response.data)
        console.log(response.data);
        // Î≥ÄÏàò submitted 
        this.submitted = true;
      })
      // Ïã§Ìå®ÌïòÎ©¥ .catch() Í≤∞Í≥ºÍ∞Ä Ï†ÑÏÜ°Îê®
      .catch(e => {
        console.log(e);
      })

    },
    newCustomer() {
      // ÏÉàÏñëÏãù Îã§Ïãú Î≥¥Ïó¨Ï£ºÍ∏∞ Ìï®Ïàò, Î≥ÄÏàò Ï¥àÍ∏∞Ìôî (Í≥†Í∞ùÎì±Î°ù ÌõÑ Ï∂îÍ∞ÄÎ°ú Îòê Í≥†Í∞ù Îì±Î°ùÌï† Îïå)
      this.submitted = false;
      this.customer = {}
    }
  },
};
</script>

<style>
  .submit-form {
    max-width: 300px;
    margin: auto;
  }
